<template>
  <div class="attrs">
    <el-drawer
      :visible.sync="this.hasCurTextComponent"
      :with-header="false"
      direction="btt"
      custom-class="attr-drawer"
      :modal="false"
      :size="'30%'"
      ref="attrRef"
      id="attr-drawer"
    >
      <div class="options">
        <span class="option" @click="handleCopyComp">
          <i class="option-icon el-icon-document-copy"></i>
        </span>
        <span class="option" @click="handleDeleteComp">
          <i class="option-icon el-icon-delete"></i>
        </span>
        <span class="option" @click="handleOnEdit">
          编辑
        </span>
        <span class="option" @click="handleShowFontsizeSlider">
          {{ (this.curComponent && this.curComponent.style.fontSize) || 0 }}px
        </span>
      </div>
      <div @touchstart="handleShowToolTip" @touchend="hadnleHideToolTip">
        <el-slider
          v-show="showFontSizeSlider"
          v-model="customFontSizeValue"
          :min="12"
          :max="60"
          @change="handleFontSizeChange"
        ></el-slider>
      </div>
    </el-drawer>
    <el-dialog
      :visible.sync="dialogVisible"
      :fullscreen="true"
      :before-close="handleCloseDialog"
      v-if="this.curComponent"
      :lock-scroll="true"
    >
      <el-input v-model="textValue" placeholder="请输入内容"></el-input>
    </el-dialog>
  </div>
</template>

<script>
import AttrList from "@/components/AttrList"; // 右侧属性列表
import { mapState, mapGetters } from "vuex";
import toast from "@/utils/toast";

export default {
  components: {
    AttrList,
  },
  computed: {
    ...mapState([
      "componentData",
      "curComponent",
      "isClickComponent",
      "canvasStyleData",
      "editor",
      "boxModelsData",
      "currentBoxModel",
      "backgroundColor",
      "menuRef",
    ]),
    ...mapGetters(["hasCurTextComponent"]),
  },
  data() {
    return {
      drawer: false,
      dialogVisible: false,
      textValue: "",
      showFontSizeSlider: false,
      customFontSizeValue: 14,
    };
  },
  watch: {
    curComponent(newData, oldData) {
      if (newData) {
        this.textValue = newData.propValue;
        this.customFontSizeValue = newData.style.fontSize;
      } else {
        this.textValue = "";
      }
    },
  },
  methods: {
    copy() {
      this.$store.commit("copy");
    },

    paste() {
      this.$store.commit("paste", false);
      this.$store.commit("recordSnapshot");
      this.$store.commit("setLatestCompActice");
    },

    deleteComponent() {
      this.$store.commit("deleteComponent");
      this.$store.commit("recordSnapshot");
      this.$store.commit("setLatestCompActice");
    },

    handleCopyComp() {
      if (this.curComponent) {
        this.copy();
        this.paste();
      } else {
        toast("请先选择组件");
        return;
      }
    },
    handleDeleteComp() {
      if (this.curComponent) {
        this.deleteComponent();
      } else {
        toast("请先选择组件");
        return;
      }
    },
    handleOnEdit() {
      this.dialogVisible = true;
    },
    handleCloseDialog() {
      this.dialogVisible = false;
      this.$store.commit("updateCurComponent", {
        ...this.curComponent,
        propValue: this.textValue,
      });
    },
    handleFontSizeChange(value) {
      this.$store.commit("updateCurComponent", {
        ...this.curComponent,
        style: {
          ...this.curComponent.style,
          fontSize: value,
        },
      });
    },
    handleShowFontsizeSlider() {
      this.showFontSizeSlider = !this.showFontSizeSlider;
    },
    handleShowToolTip() {
      const toolTip = document.getElementsByClassName("el-tooltip__popper")[0];
      if (toolTip) {
        toolTip.style.visibility = "visible";
      }
    },
    hadnleHideToolTip() {
      const toolTip = document.getElementsByClassName("el-tooltip__popper")[0];
      if (toolTip) {
        toolTip.style.visibility = "hidden";
      }
    },
  },
};
</script>
