<template>
  <div class="menu">
    <div class="menu-btn" @click="drawer = true">
      <span class="content">+</span>
    </div>
    <el-drawer
      title="我是标题"
      :visible.sync="drawer"
      :with-header="false"
      direction="btt"
      custom-class="menu-drawer"
    >
      <div class="options">
        <span class="option" @click="handleAddTextComponent">
          <i class="option-icon el-icon-edit-outline"></i>
          文字
        </span>
        <span class="option" @click="showBackGroundSelector = true">
          <i class="option-icon el-icon-picture-outline"></i>
          背景
        </span>
        <span class="option" @click="showUploadDrawer = true">
          <i class="option-icon el-icon-upload2"></i>
          上传
        </span>
      </div>
    </el-drawer>
    <el-drawer
      title="背景"
      :visible.sync="showBackGroundSelector"
      :with-header="false"
      direction="btt"
      custom-class="menu-drawer"
    >
      <ColorSelector />
    </el-drawer>
    <el-drawer
      title="上传"
      :visible.sync="showUploadDrawer"
      :with-header="false"
      direction="btt"
      custom-class="menu-drawer"
    >
      <el-upload
        class="upload-wrap"
        action="https://zx.carryba.com/addons/zjhj_mall/core/web/index.php?r=upload/save-img"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :on-success="handleUploadSuccess"
        :file-list="fileList"
        list-type="picture"
      >
        <p class="header">
          <el-button size="small" type="primary">上传图片</el-button>
          <el-button size="small" type="primary" @click="handleUsePic"
            >使用</el-button
          >
        </p>
        <div slot="tip" class="el-upload__tip">
          只能上传jpg/png文件，且不超过500kb
        </div>
      </el-upload>
    </el-drawer>
  </div>
</template>

<script>
import generateID from "../../utils/generateID";
import ColorSelector from "../BackGroundColor.vue";

export default {
  components: { ColorSelector },
  data() {
    return {
      drawer: false,

      showBackGroundSelector: false,
      showUploadDrawer: false,
      fileList: [],
      selectedPic: null,
      textComponentData: {
        animations: [],
        events: {},
        groupStyle: {},
        isLock: false,
        component: "v-text",
        label: "托动添加标题",
        propValue: "点击编辑文字",
        icon: "wenben",
        style: {
          rotate: 0,
          opacity: 1,
          width: 110,
          height: 22,
          fontSize: 18,
          fontWeight: 500,
          lineHeight: "",
          letterSpacing: 0,
          textAlign: "",
          color: "#000",
          top: 571,
          left: 356,
        },
      },
      picComponentData: {
        animations: [],
        events: {},
        groupStyle: {},
        isLock: false,
        component: "Picture",
        label: "图片",
        icon: "tupian",
        propValue: "img/title.ac55a42f.jpg",
        style: {
          rotate: 0,
          opacity: 1,
          width: 300,
          height: 200,
          borderRadius: "",
          top: 250,
          left: 425,
        },
        id: 0,
      },
    };
  },
  created() {
    this.fileList = JSON.parse(localStorage.getItem("userPics")) || [];
  },
  methods: {
    handleDragStart(e) {
      e.dataTransfer.setData("index", 1);
    },

    handleAddTextComponent(e) {
      const component = this.textComponentData;
      const parentDom = document.getElementById("center");
      const parWidth = parentDom.clientWidth;
      const parHeight = parentDom.clientHeight;
      component.style.top = 300;
      component.style.left = 50;
      component.id = generateID();
      this.$store.commit("addComponent", { component });
      this.$store.commit("recordSnapshot");
      this.drawer = false;
    },
    handleRemove(file, fileList) {
      // console.log("handleRemove", file, fileList);
    },
    handlePreview(file) {
      // console.log("handlePreview", file);
      this.selectedPic = file;
    },
    handleUploadSuccess(response, file, fileList) {
      if (fileList) {
        const formatFileList = fileList.map((item) => ({
          name: item.name,
          url: item.response || item.url,
        }));
        this.fileList = formatFileList;
        localStorage.setItem("userPics", JSON.stringify(formatFileList));
      }
    },
    handleUsePic(e) {
      try {
        e.preventDefault();
        e.stopPropagation();
        const component = {
          ...this.picComponentData,
          propValue: this.selectedPic.url,
        };
        const parentDom = document.getElementById("center");
        const parWidth = parentDom.clientWidth;
        const parHeight = parentDom.clientHeight;
        component.style.top = 300;
        component.style.left = 50;
        component.id = generateID();
        this.$store.commit("addComponent", { component });
        this.$store.commit("recordSnapshot");
        this.drawer = false;
        this.showBackGroundSelector = false;
        this.showUploadDrawer = false;
      } catch (error) {
        console.log("error", error);
      }
    },
  },
};
</script>

