<template>
  <div class="home">
    <Toolbar />
    <main>
      <section id="center" class="center">
        <div class="cut-canvas" id="cutCanvas">
          <BoxmodelView />
          <div
            class="content"
            id="content"
            @drop="handleDrop"
            @dragover="handleDragOver"
            @mousedown="handleMouseDown"
            @mouseup="deselectCurComponent"
          >
            <Editor />
          </div>
        </div>
      </section>
    </main>
    <Menu ref="menuRef" />
    <Attrs />
  </div>
</template>

<script>
import Editor from "@/components/Editor/index";
// import Menu from "@/components/Menu";
import AttrList from "@/components/AttrList"; // 右侧属性列表
import AnimationList from "@/components/AnimationList"; // 右侧动画列表
import EventList from "@/components/EventList"; // 右侧事件列表
// import componentList from "@/custom-component/component-list"; // 左侧列表数据
import { List } from "../custom-component/component-list";
import Toolbar from "@/components/Toolbar";
import { deepCopy } from "@/utils/utils";
import { mapState } from "vuex";
import generateID from "@/utils/generateID";
import { listenGlobalKeyDown } from "@/utils/shortcutKey";
import BoxmodelView from "../components/BoxModelView";
import Menu from "../components/Menu/index.vue";
import Attrs from "../components/Attrs/index.vue";
export default {
  components: {
    Editor,
    AttrList,
    AnimationList,
    EventList,
    Toolbar,
    BoxmodelView,
    Menu,
    Attrs,
  },
  data() {
    return {
      activeName: "canvas",
      reSelectAnimateIndex: undefined,
      needToChange: [
        "top",
        "left",
        "width",
        "height",
        "fontSize",
        "borderWidth",
      ],
      timer: null,
      scale: 20,
    };
  },
  computed: mapState([
    "componentData",
    "curComponent",
    "isClickComponent",
    "canvasStyleData",
    "editor",
    "boxModelsData",
    "currentBoxModel",
    "backgroundColor",
  ]),
  created() {
    this.restore();
    // 全局监听按键事件
    listenGlobalKeyDown();
    // console.log("this.canvasStyleData.scale", this.canvasStyleData.scale);
    this.scale = this.canvasStyleData.scale;
  },
  mounted() {
    this.handleInitCanvasWrapStyle();
  },
  methods: {
    format(value) {
      const scale = this.scale;
      return (value * parseInt(scale)) / 100;
    },

    getOriginStyle(value) {
      const scale = this.canvasStyleData.scale;
      const result = value / (parseInt(scale) / 100);
      return result;
    },

    handleScaleChange() {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        // 画布比例设一个最小值，不能为 0
        // eslint-disable-next-line no-bitwise
        this.scale = ~~this.scale || 1;
        const componentData = deepCopy(this.componentData);
        componentData.forEach((component) => {
          Object.keys(component.style).forEach((key) => {
            if (this.needToChange.includes(key)) {
              // 根据原来的比例获取样式原来的尺寸
              // 再用原来的尺寸 * 现在的比例得出新的尺寸
              // 不能用 Math.round 防止 1 px 的边框变 0
              component.style[key] = Math.ceil(
                this.format(this.getOriginStyle(component.style[key]))
              );
            }
          });
        });

        this.$store.commit("setComponentData", componentData);
        this.$store.commit("setCanvasStyle", {
          ...this.canvasStyleData,
          scale: this.scale,
        });
      }, 1000);
    },

    handleSetBoxModelsData(e, type) {
      console.log("boxLengthChange", e, type);
      this.$store.commit("setBoxModelsData", {
        [type]: +e.target.value,
      });
    },

    handleInitCanvasWrapStyle() {
      if (!localStorage.getItem("canvasStyle")) {
        const width =
          document.getElementById("center").clientWidth - 20 - 20 - 2;
        const height =
          document.getElementById("center").clientHeight - 20 - 20 - 2;
        this.$store.commit("setCanvasStyle", {
          width: width / (this.scale / 100),
          height: height / (this.scale / 100),
        });
      }
    },
    restore() {
      // 用保存的数据恢复画布
      if (localStorage.getItem("canvasData")) {
        this.$store.commit(
          "setComponentData",
          this.resetID(JSON.parse(localStorage.getItem("canvasData")))
        );
      }

      if (localStorage.getItem("canvasStyle")) {
        this.$store.commit(
          "setCanvasStyle",
          JSON.parse(localStorage.getItem("canvasStyle"))
        );
      }
    },

    resetID(data) {
      data.forEach((item) => {
        item.id = generateID();
      });

      return data;
    },

    handleDrop(e) {
      e.preventDefault();
      e.stopPropagation();
      const index = e.dataTransfer.getData("index");
      const rectInfo = this.editor.getBoundingClientRect();
      if (index) {
        const component = deepCopy(List[index]);
        component.style.top = e.clientY - rectInfo.y;
        component.style.left = e.clientX - rectInfo.x;
        component.id = generateID();
        this.$store.commit("addComponent", { component });
        this.$store.commit("recordSnapshot");
      }
    },

    handleDragOver(e) {
      e.preventDefault();
      e.dataTransfer.dropEffect = "copy";
    },

    handleMouseDown() {
      this.$store.commit("setClickComponentStatus", false);
    },

    deselectCurComponent(e) {
      if (!this.isClickComponent) {
        this.$store.commit("setCurComponent", { component: null, index: null });
      }

      // 0 左击 1 滚轮 2 右击
      if (e.button != 2) {
        this.$store.commit("hideContextMenu");
      }
    },

    handleOpenMenu() {},
  },
};
</script>

